.add-task-button {
  display: flex;
  align-items: center;
  margin-top: 1rem;
  cursor: pointer;
  &:hover {
    .plus {
      background: #de4c4a;
      color: white;
    }
    p {
      color: #de4c4a;
    }
  }
  .plus {
    height: 20px;
    width: 20px;
    border-radius: 50%;
    color: #de4c4a;
    display: flex;
    justify-content: center;
    align-items: center;
    svg {
      transform: translate(-1px, -1px);
    }
  }
  .add-task-text {
    margin-left: 0.5rem;
    color: grey;
    font-size: 14px;
    font-weight: 300;
    transform: translate(-1px, -1px);
  }
}

.add-task-form {
  margin-top: 1rem;

  .container {
    background: var(--light-black);
    border: 1px solid hsla(0, 0%, 100%, 0.1);
    border-radius: 3px;
    cursor: text;
    padding: 0.5rem 1rem 0.8rem;
    &:hover {
      border: 1px solid #555;
    }
    div {
      display: flex;
      label {
        font-size: 14px;
        height: 32px;
        display: flex;
        align-items: center;
        width: fit-content;
      }
      input {
        display: flex;
        align-items: center;
        margin-left: 0.5rem;
        background: none;
        width: 100%;
        outline: none;
        border: none;
        color: var(--text-white);
        resize: none;
        height: 32px;
        margin-bottom: 0.2rem;
        transform: translateY(1.3px);
      }
    }
    .sub-options {
      .priority {
        align-items: center;
        .priority-select {
          margin-left: 0.5rem;
          background: var(--dark-gray);
          color: var(--text-white);
          border-radius: 3px;
          outline: none;
          border: 1px solid hsla(0, 0%, 100%, 0.1);
          height: 32px;
          padding: 2px 3px;
          transform: translateY(1.8px);
          &:hover {
            border: 1px solid #555;
          }
          &:focus {
            border: 1px solid #555;
          }
        }
      }
      .date {
        margin-left: 1.5rem;
        align-items: center;
        label {
          width: 110px;
        }
        input {
          margin-left: 0.5rem;
          background: var(--dark-gray);
          color: var(--text-white);
          border-radius: 3px;
          outline: none;
          border: 1px solid hsla(0, 0%, 100%, 0.1);
          padding: 2px 3px;
          transform: translateY(1.8px);
          &:hover {
            border: 1px solid #555;
          }
          &:focus {
            border: 1px solid #555;
          }
        }
      }
      .project-selection {
        margin-left: 1.5rem;
        align-items: center;
        select {
          margin-left: 0.5rem;
          background: var(--dark-gray);
          color: var(--text-white);
          border-radius: 3px;
          outline: none;
          border: 1px solid hsla(0, 0%, 100%, 0.1);
          padding: 2px 3px;
          transform: translateY(1.8px);
          height: 32px;
          &:hover {
            border: 1px solid #555;
          }
          &:focus {
            border: 1px solid #555;
          }
        }
      }
    }
  }
  .add-task-button {
    cursor: pointer;
    margin: 0.5rem 0;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    padding: 6px 14px 7px 12px;
    position: relative;
    display: inline-block;
    white-space: nowrap;
    border-radius: 3px;
    text-decoration: none;
    text-align: center;
    background: #de4c4a;
    border: 1px solid transparent;
    outline: none;
    color: #fff;
    transition: 0.2s ease-in-out;

    &:hover {
      filter: brightness(1.1);
    }
  }
  .cancel {
    margin-left: 1rem;
    cursor: pointer;
    margin: 0.5rem 0;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    padding: 6px 14px 7px 12px;
    position: relative;
    display: inline-block;
    white-space: nowrap;
    border-radius: 3px;
    text-decoration: none;
    text-align: center;
    border: 1px solid transparent;
    outline: none;
    color: #fff;
    background: none;
    transition: 0.2s ease-in-out;

    &:hover {
      filter: brightness(1.1);
      background: #303030;
    }
  }
}

.completed {
  opacity: 0.5;
  text-decoration: line-through;
}
